import { useQuery } from "@tanstack/react-query";
import { getCards } from "../../apis";
import { Grid } from "@mui/material";

const Cards = () => {
  const { isLoading, data, error } = useQuery(
    ["Cards"],
    () => getCards("s"),
    {
      staleTime: Infinity,
      retry: false,
    }
  );

  if (isLoading) return "Loading...";

  if (error) return "An error has occurred: " + error.message;

  return (
    <Grid container width={"500px"}>
      {data.Cards.map((el, idx) => (
        <Grid item xs={12} key={idx}>
          <Grid>
            {el.Slot} : {el.Name} ( {el.Grade} )
          </Grid>
          {/* <img src={el.Icon} alt={""} /> */}
          <Grid>
             : {el.AwakeCount} / {el.AwakeTotal}
          </Grid>
          <Grid>{console.log(JSON.parse(el.Tooltip))}</Grid>
        </Grid>
      ))}
      {data.Effects.map((el, idx) => (
        <Grid key={el.Index}>
          <Grid>   : {el.CardSlots.toString()}</Grid>
          <Grid>
            
            <br />
            {el.Items.length > 0 &&
              el.Items.map((set, idx) => <Grid key={idx}>{set.Name}</Grid>)}
          </Grid>
        </Grid>
      ))}
    </Grid>
  );
};

export default Cards;
