import { Grid, Tooltip, Typography } from "@mui/material";
import Label from "./components/Label";
import { getArmorProfiles } from "../../apis";
import { useQuery } from "@tanstack/react-query";

const Info = () => {
  const { data } = useQuery(["Profiles"], () => getArmorProfiles("s"));

  return (
    <>
      <Grid container sx={{ width: "500px" }}>
        <Grid item xs={12}>
          <Label label={""} contents={data.CharacterClassName} />
          <Label label={""} contents={data.CharacterLevel} />
          <Label label={""} contents={data.CharacterName} />
          <Label label={""} contents={data.Title} />
          <Label label={""} contents={data.ExpeditionLevel} />
          <Label label={""} contents={data.GuildName} />
          <Label label={""} contents={data.GuildMemberGrade} />
          <Label label={""} contents={data.TownName} />
          <Label label={""} contents={data.TownLevel} />
          <Label label={"()"} contents={data.ItemAvgLevel} />
          <Label label={"()"} contents={data.ItemMaxLevel} />
          <Label label={"PVP"} contents={data.PvpGradeName} />
          <Label label={""} contents={data.ServerName} />

          <Grid container>
            <Grid item xs={2}>
              
            </Grid>
            <Grid item xs={6}>
              {data.Stats.map((el, idx) => (
                <Tooltip
                  key={idx}
                  title={
                    <>
                      {el.Tooltip.map((dom, idxKey) => (
                        <Grid
                          key={el.Type + idxKey}
                          dangerouslySetInnerHTML={{ __html: dom }}
                        />
                      ))}
                    </>
                  }
                  placement="right"
                >
                  <Grid container>
                    <Grid item xs={4}>
                      <Typography>{el.Type}</Typography>
                    </Grid>
                    <Grid item>
                      <Typography>{el.Value}</Typography>
                    </Grid>
                  </Grid>
                </Tooltip>
              ))}
            </Grid>
          </Grid>
        </Grid>
        {/* <div>
            {data.Tendencies.map((el, idx) => (
              <div key={el.Type + idx}>
                {el.Type} : {el.Point}
              </div>
            ))}
          </div> */}
      </Grid>
    </>
  );
};

export default Info;
