import {
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
} from "@mui/material";
import { useState } from "react";
import Equipment from "./Equipment";
import Engrabving from "./Engrabving";
import Cards from "./Cards";
import Gems from "./Gems";
import Info from "./Info";

const SpecChk = () => {
  const [keyName, setKeyName] = useState("");
  const [anchor, setAnchor] = useState(false);

  const pages = [
    { key: "info", drawerPage: <Info />, chkName: "" },
    { key: "equip", drawerPage: <Equipment />, chkName: "" },
    { key: "engrabving", drawerPage: <Engrabving />, chkName: "" },
    { key: "gems", drawerPage: <Gems />, chkName: "" },
    { key: "card", drawerPage: <Cards />, chkName: "" },
  ];

  const isOpen = (key, isChk) => {
    setKeyName(key);
    setAnchor(isChk);
  };

  return (
    <>
      <List>
        {pages.map((el, idx) => (
          <ListItem key={idx}>
            <ListItemButton onClick={() => isOpen(el.key, true)}>
              <ListItemText primary={el.chkName} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Drawer anchor={"right"} open={anchor} onClose={() => setAnchor(false)}>
        {pages.find((el) => el.key === keyName)?.drawerPage}
      </Drawer>
    </>
  );
};

export default SpecChk;
